<template>
    <div class="v-knowledge-index" v-loading="loading">
        <!-- ÊêúÁ¥¢ÁªìÊûú -->
        <knowledgeList v-if="search" :list="list" :total="total" :pagination="pagination" @onPageKey="onPageKey" />
        <template v-else>
            <!-- Âø´Êç∑ÂÖ•Âè£ -->
            <!-- <wikiEntry /> -->
            <!--  Áé©Ê≥ïÊåáÂçó -->
            <wikiGuide />
            <!-- ÁÉ≠Èó®ÂâßÊÉÖ -->
            <!-- <wikiHot /> -->
            <!-- ÊúØËØ≠Â§ßÂÖ® -->
            <wikiJargon />
            <!-- ÊúÄÊñ∞Êõ¥Êñ∞ -->
            <wikiRecent />
        </template>
    </div>
</template>

<script>
import knowledgeList from "@/components/knowledge/list.vue";
// import wikiEntry from "@/components/knowledge/entry.vue"; // Âø´Êç∑ÂÖ•Âè£
import wikiGuide from "@/components/knowledge/guide.vue"; // Áé©Ê≥ïÊåáÂçó
// import wikiHot from "@/components/knowledge/hot.vue"; // ÁÉ≠Èó®ÂâßÊÉÖ
import wikiJargon from "@/components/knowledge/jargon.vue"; // ÊúØËØ≠Â§ßÂÖ®
import wikiRecent from "@/components/knowledge/recent.vue"; // ÊúÄÊñ∞Êõ¥Êñ∞
import { getKnowledgeList } from "@/service/knowledge.js";

export default {
    name: "KnowledgeIndex",
    components: {
        knowledgeList,
        // wikiEntry,
        // wikiHot,
        wikiJargon,
        wikiGuide,
        wikiRecent,
    },
    data: function () {
        return {
            loading: false,
            search: "",
            list: "",

            page: 1,
            per: 20,
            total: 1,
            pages: 1,
        };
    },
    computed: {
        params() {
            let params = {
                per: this.per,
                page: this.page,
                _search: this.search,
            };
            return params;
        },
        pagination() {
            return {
                page: this.page,
                total: this.total,
                per: this.per,
            };
        },
        routeSearch() {
            return this.$route.query.search;
        },
    },
    watch: {
        params() {
            this.getSearchData();
        },
    },
    methods: {
        // Ëé∑ÂèñÊêúÁ¥¢ÁöÑÊï∞ÊçÆ
        getSearchData() {
            this.loading = true;
            getKnowledgeList(this.params)
                .then((res) => {
                    this.list = res.data.data.list || [];
                    this.total = res.data.data.total;
                })
                .finally(() => {
                    this.loading = false;
                });
        },

        // ===============
        // ÊêúÁ¥¢ÂÖ≥ÈîÆËØçÊü•Êâæ
        onSearchKey(val) {
            this.search = val;
            this.page = 1;
        },
        // ÂàáÊç¢È°µÁ†Å
        onPageKey(val) {
            this.page = val;
        },
    },
    created: function () {},
};
</script>
